export default {
	data() {
		return {};
	},
	props: {
		bannerHeight: {
			default: "300"
		},
		bannerType: {
			default: '0'
		},
		bannerList: {
			default: [],
		},
		sList: {
			default: []
		},
		muti: {
			default: false,
		}
	},
	computed: {
		bannerHeightComputed() {
			return uni.upx2px(this.bannerHeight) + 'px';
		}
	},
	methods: {
		goPage(url) {
			if (url == '/pages/subPages/fanclub_list/fanclub_list' && !this.$app.getData('userStar').id) {
				this.$app.toast('请先加入一个圈子')
				return
			}
			this.$app.goPage(url)
		}
	}
}
